@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font: 400 14px Roboto, sans-serif;
  background-color:white;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

input, textarea{
  
    height: 2.5rem;
    margin-top: 0.1rem;
    border-radius: 0.4rem;
    background-color: #f3f3f3;
    border: 1px solid hsl(240, 18%, 80%);
    outline: 0;
    padding: 0 1.6rem;
    font: 1rem Roboto; 
}

.check-item:focus-within::after{
  width: calc(100% - 3.2rem);
  height: 2px;
  content: '';
  background: #084B8A;
  position: absolute;
  left: 1.6rem;
  right: 1.6rem;
  bottom: 0;
}

button {
  height: 2.5rem;
  border-radius: 0.4rem;
  background: #f3f3f3;
  border: 1px solid hsl(240, 18%, 80%);
  padding: 0 1.0rem;
  font: 1rem Roboto; 
  cursor: pointer;
  transition: background-color 0.4s;
}

button:hover {
  background: #e9e7e7;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.each-slide > div {
    display: flex;
    align-items: center;
    justify-content: center;
    background-size: cover;
    height: 450px;
  }
  
  .each-slide span {
    padding: 20px;
    font-size: 20px;
    background: #efefef;
    text-align: center;
    opacity: 70%;
  }
.top-contact {
    display: flex;
    padding: 10px;
    height: 40px;
    color: white;
    justify-content: space-between;
    background-color: #084B8A;
    font:Roboto;
    
    box-shadow: 5px 5px 5px 2px #888888;
}

.top-contact h3 {
    margin-top: -4px;
    padding: 10px;
    margin-left: -380px;
}
.top-contact h1 {
    margin-top: -5px;
    text-align: left;
}

.top-contact h4 {
    margin-top: 0px;
    text-align: left;
    padding-right: 10px;
    padding-left: 10px;
}

.reservar {
    line-height:30px;
    justify-items: right;
    display: flex;
    flex-direction: row;
    margin-right: 10px;
    align-content: center;
}


.menu {
    
    height: 30px;
    display: flex;
    justify-content: center;
    align-content: space-between;
    padding: 30px;
    font-family: 'Montserrat', sans-serif;
}

.menu a {
    display: table-cell;
    padding: 15px;
    text-decoration: none;
    color: black;
    font-size: 17px;
    transition: opacity 0.5s;
  }

  .menu a:hover {
    opacity: 0.6;
  }



.footer {
    display: flex;
    padding: 7px;
    height: 150px;
    color: white;
    justify-content: space-between;
    background-color: #084B8A;
    font:Roboto;
    /* margin-bottom: 0px; */
    /* box-shadow: 5px 5px 5px 2px #888888; */
}

.footer div {
    text-align: left;
    margin-right: 30px;
}




